'use client'

import HowItWorks from './howItWorks'
import CoreFeature from './coreFeature'
import CTASection from '../home/CTASection'

const FeatureComponent = () => {
    return (
        <div>

            {/* ---------- HERO ---------- */}
            <section className="max-w-7xl mx-auto px-6 py-20 text-center">
                <h1 className="text-4xl md:text-5xl font-bold text-primary mb-6">
                    Everything You Need to Build Customer Loyalty
                </h1>
                <p className="text-lg text-gray-600 max-w-3xl mx-auto">
                    Stampi helps local businesses reward customers, increase repeat visits,
                    and grow without complicated apps or hardware.
                </p>
            </section>

            {/* ---------- CORE FEATURES ---------- */}
            <CoreFeature/>

            {/* ---------- HOW IT WORKS ---------- */}
            <HowItWorks/>

            {/* ---------- BENEFITS ---------- */}
            <section className="bg-light-shade/60 py-20">
                <div className="max-w-4xl mx-auto px-6 text-center">
                    <h2 className="text-3xl font-semibold mb-6">
                        Built for Local Businesses
                    </h2>
                    <p className="text-gray-600 text-lg">
                        Whether you run a café, salon, grocery store, or retail shop,
                        Stampi helps you build loyalty without extra work.
                    </p>
                </div>
            </section>

            {/* ---------- CTA ---------- */}
            <CTASection/>

        </div>
    )
}

export default FeatureComponent